@import 'styles/globals.scss';

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding: $base-step flexwidth(10, $horizontal-section-width-m);
    width: flexwidth(350, $horizontal-section-width-m);
    max-width: $form-max-width;
    min-width: $form-min-width;
    background-color: white;

    @media screen and (min-width: $desktop) {
        padding: $base-step * 5.5 flexwidth(100, $horizontal-section-width-d);
        width: flexwidth(($form-max-width / 1px), $horizontal-section-width-d);
        border-radius: $base-step * 2;
        box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
    }

    &__title {
        margin: 0 0 (50/30) * 1em;
        font-size: 3em;
        line-height: (35/30);
    }

    &__fieldset {
        margin-bottom: $base-step * 1.5;
        width: 100%;
        background-color: transparent;
        outline: none;
        border: none;
    }

    &__link {
        text-decoration: none;
        transition: color 0.15s ease-in-out;
        cursor: pointer;
    }

    &__recall {
        @extend %basic-form-font-settings;

        margin-bottom: $base-step;
        width: 100%;
        color: $color-form-subtle;
        text-align: right;

        .form__link {
            color: $color-form-subtle;

            &:hover {
                color: darken($color-form-subtle, 25%);
            }

            &:active {
                color: darken($color-form-subtle, 40%);
            }
        }
    }

    &__register {
        @extend %basic-form-font-settings;

        color: $color-form-subtle;

        .form__link {
            color: $color-form-subtle-link;

            &:hover {
                color: darken($color-form-subtle-link, 25%);
            }
        }
    }
}
